<template>
  <div>
    <el-card shadow='hover' class='card'>
      <el-table ref="filterTable" :data="list" stripe class='table' @selection-change="handleSelectionChange"
        v-if='!listEmpty'>
        <el-table-column type='selection' fixed></el-table-column>

        <el-table-column prop='time' label='时间' sortable min-width="180"></el-table-column>
        <el-table-column prop='type' label='类别' :formatter="typeFormatter" :filter-method="filterHandler"
          :filters='filter.type' min-width="130"></el-table-column>
        <el-table-column prop='content' label='内容' :formatter="contentFormatter" min-width="300"></el-table-column>
        <el-table-column prop='link' label='跳转' min-width="60">\
          <template slot-scope="scope">
            <span class='link' @click='openPage(scope.row.link, scope.row.alertid)' v-if='scope.row.link != ""'>
              <el-image :src='require("@/../public/assets/navigate.png")' style='width:25px'></el-image>
            </span>
            <span v-if='scope.row.link == ""'>
              <el-image :src='require("@/../public/assets/forbid.png")' style='width:25px'></el-image>
            </span>
          </template>
        </el-table-column>
        <el-table-column prop='read' label='阅读' :formatter="readFormatter" :filter-method="filterHandler"
          :filters='filter.read' min-width="100"></el-table-column>
      </el-table>

      <div class='empty' v-if='listEmpty'>
        <el-image :src="require('@/../public/assets/empty.png')" style='width:100px'></el-image>
        <div>{{$t('Admin.ListEmpty')}}</div>
      </div>
    </el-card>

    <div class='floatButton'>
      <el-button circle type='success' icon='el-icon-refresh' class='floatButton floatButtonRefresh'
        @click="refresh"></el-button>
      <el-button circle type='warning' icon='el-icon-check' class='floatButton floatButtonMark'
        @click="markRead"></el-button>
    </div>

  </div>
</template>

<script>
import axios from 'axios'
import Cookies from 'js-cookie'
import * as server from '../../../server.js'

export default {
  name: 'alert-list',
  data () {
    //TYPE   0: auth  1: reported  3: hollow comment  4: market bought  5: laf claimed 
    return {
      list: [],
      listEmpty: true,
      filter: {
        read: [{ text: this.$t('Alert.Unread'), value: this.$t('Alert.Unread') }],
        type: [{ text: this.$t('Alert.Auth'), value: this.$t('Alert.Auth') }, { text: this.$t('Alert.Report'), value: this.$t('Alert.Report') }, { text: this.$t('Alert.Hollow'), value: this.$t('Alert.Hollow') }, { text: this.$t('Alert.Market'), value: this.$t('Alert.Market') }, { text: this.$t('Alert.Laf'), value: this.$t('Alert.Laf') }]
      },
      multipleSelection: [],
    }
  },
  methods: {
    refresh () {
      this.getAlertList();
    },
    markRead () {
      let readLists = [];
      for (let i in this.multipleSelection) {
        if (this.multipleSelection[i].read == false) {
          readLists.push(this.multipleSelection[i].alertid);
        }
      }
      if (readLists.length > 0) {
        const loadingInstance = this.loading();

        axios({
          method: 'post',
          url: server.default.updateAlertList,
          data: new URLSearchParams({
            'cookie': Cookies.get('loginAs'),
            'list': readLists
          })
        }).then((response) => {
          loadingInstance.close();
          if (response.status != 200) {
            this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
          }
          else {
            if (response.data == 'ALERT_UPDATE_COMPLETE') {
              this.$bus.$emit('readAlert', readLists.length)
              this.redirect('alertList');
            }
          }
        });
      }
    },
    getAlertList () {
      const loadingInstance = this.loading();

      axios({
        method: 'post',
        url: server.default.getAlertList,
        data: new URLSearchParams({
          'cookie': Cookies.get('loginAs')
        })
      }).then((response) => {
        loadingInstance.close();
        if (response.status != 200) {
          this.alert(this.$t('System.NetworkIssue'), this.$t('System.Error'))
        }
        else {
          if (response.data.status == 'ALERT_GETLIST_COMPLETE') {
            if (response.data.list.length > 0) {
              this.listEmpty = false;
              for (let item of response.data.list) {
                this.list.push({
                  alertid: item.alertid,
                  userid: item.userid,
                  type: item.type,
                  content: item.content == '' ? item.content : JSON.parse(item.content),
                  time: this.formatTime(item.time),
                  read: item.read,
                  link: item.link
                })
                if (item.type == 4) {
                  this.list[this.list.length - 1].link = 'market';
                }
                else if (item.type == 5) {
                  this.list[this.list.length - 1].link = 'laf';
                }
              }
            }
            else {
              this.listEmpty = true;
            }
          }
        }
      });
    },
    openPage (link, id) {
      if (link == 'market' || link == 'laf') {
        this.$bus.$emit('showAlertDetail', id)
      }
      else {
        window.open(link, '_self');
      }
    },
    contentFormatter (row) {
      if (row.type == 0) {
        if (row.content == 0) {
          return this.$t('Alert.ReportFail');
        }
        else if (row.content == 1) {
          return this.$t('Alert.ReportSuccess');
        }
      }
      else if (row.type == 1) {
        if (row.content == 0) {
          return this.$t('Alert.AuthFail');
        }
        else if (row.content == 1) {
          return this.$t('Alert.AuthSuccess');
        }
      }
      else if (row.type == 3) {
        return this.$t('Alert.HollowCommentNew');
      }
      else if (row.type == 4) {
        return this.$t('Alert.MarketPurchased1') + row.content.itemname + this.$t('Alert.MarketPurchased2')
      }
      else if (row.type == 5) {
        return this.$t('Alert.LafClaimed1') + row.content.name + this.$t('Alert.LafClaimed2')
      }
      else if (row.type == 6) {
        return this.$t('Alert.MarketPurchasedPlace1') + row.content.name + this.$t('Alert.MarketPurchasedPlace2')
      }
    },
    typeFormatter (row) {
      if (row.type == 0) {
        return this.$t('Alert.Report');
      }
      else if (row.type == 1) {
        return this.$t('Alert.Auth');
      }
      else if (row.type == 3) {
        return this.$t('Alert.Hollow');
      }
      else if (row.type == 4) {
        return this.$t('Alert.Market');
      }
      else if (row.type == 5) {
        return this.$t('Alert.Laf');
      }
      else if (row.type == 6) {
        return this.$t('Alert.MarketPlace');
      }
    },
    readFormatter (row) {
      if (row.read == 0) {
        return this.$t('Alert.Unread');
      }
      else if (row.read == 1) {
        return this.$t('Alert.Read');
      }
    },
    filterHandler (value, row, column) {
      const property = column['property'];
      if (property == 'type') {
        return this.typeFormatter(row) == value;
      }
      else if (property == 'read') {
        return this.readFormatter(row) == value;
      }
    },
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange (val) {
      this.multipleSelection = val;
    }
  },
  mounted () {
    this.getAlertList();
  },
  beforeDestroy () {

  }
}
</script>

<style scoped>
.card {
  width: 90%;
  margin-left: 5%;
  border-radius: 15px;
  padding-top: 20px;
  min-height: 800px;
}
.table {
  width: 94%;
  margin-left: 3%;
  font-size: 20px;
}
.link {
  cursor: pointer;
}
.floatButton {
  position: fixed;
  right: 100px;
  font-size: 25px;
  cursor: pointer;
}
.floatButtonRefresh {
  bottom: 160px;
}
.floatButtonMark {
  bottom: 80px;
}
.empty {
  margin-top: 280px;
}
</style>